@import './mixins.scss';

.projects{
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    width: 100%;
    padding: 2em 0;
    @include container();
    // border: 1px solid red;

    &-title{
        width: 95%;
        padding: 0 0 .3em 0;
        h1{ 
            font-size: 1.7em;
            font-family: 'Quicksand', sans-serif;
        }
    }

    &-container{
        display: flex;
        flex-flow:  row wrap;
        justify-content: space-evenly;
        align-items: center;
        padding: 1em 0;
        width: 100%;
        border-bottom: 1px solid #ffff;
        border-radius: 9px;
        margin: 1em auto;

        &:last-child{
            border: none;
        }

        .title-image-container{
            display: flex;
            flex-flow: column wrap;
            justify-content: center;
            align-items: center;
            width: 50%;

            @include breakpoint-down(small){
                width: 100%;
            }

            h2{
                text-align: center;
                font-size: 1.5em;
                padding: .5em 0;
            }
    
            .project-images{
                width: 15.625em;
                height: 15.625em;
                object-fit: cover;
                object-position: center;
                border-radius: 8px;
                text-align: center;
    
                &:hover{
                    opacity: 0.3;
                }
            }
        }

        .info{
            // margin: 1em 5em;
            width: 50%;
            @include breakpoint-down(small){
                margin: .5em .8em;
                width: 100%;
            }
        }
    }
}
